import React from "react";
import { BsSearch } from "react-icons/bs";
import { BsBookmarks } from "react-icons/bs";
import { BiBell } from "react-icons/bi";
import { useDispatch, useSelector } from "react-redux";
import "./Styling/main.css";
import { getProfile, getSearch, googleAuth } from "../Redux/signup/action";
import { Link, useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import { TrendingStories } from "./TrendingStories";
import styles from "./Styling/LandingPage.module.css";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { FaTwitter } from "react-icons/fa";
import { BsBell } from "react-icons/bs";
import Bottom from "./Bottom";
import SideNavbar from "./SideNavbar";
import { greetingTime } from "greeting-time";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      padding: "10px",
    },
  },
}));

function Main() {
  const data = useSelector((state) => state.signup.data);
  console.log(data);
  const [query, setQuery] = React.useState("");
  const classes = useStyles();
  const blogs = useSelector((state) => state.signup.blog);
  const dispatch = useDispatch();
  const greetingTime = require("greeting-time");
  const [open, setOpen] = React.useState(false);
  const getBlogs = () => {
    dispatch(getSearch());
  };

  React.useEffect(() => {
    getBlogs();
    dispatch(getProfile());
  }, [dispatch, data]);

  // React.useEffect(() => {
  //   dispatch(getProfile());
  // }, [dispatch]);

  const history = useHistory();

  const enterPress = (query) => {
    history.push(`/search/${query}`);
  };
  return (
    <div className="main">
      <div className="nav">
        <div className="med">
          <img
            src="https://miro.medium.com/max/8978/1*s986xIGqhfsN8U--09_AdA.png"
            alt="medium"
          />
          <h2>{greetingTime(new Date())}</h2>
        </div>
        <div className="sec">
          <input
            type="text"
            placeholder="Search Medium"
            onChange={(e) => setQuery(e.target.value)}
          />
          <BsSearch className="searchIcon" onClick={() => enterPress(query)} />
          <Link to="/book">
            <BsBookmarks className="searchIcon" />
          </Link>
          <BsBell className="ico" />
          <Button variant="outlined" color="primary">
            Upgrade
          </Button>
        </div>
        <div onClick={() => setOpen((prev) => !prev)}>
          <img
            src={data.imageUrl}
            alt="hi"
            style={{
              borderRadius: "50%",
              width: "40px",
              height: "40px",
              margin: "15px 0px 10px 0px",
              textAlign: "left",
            }}
          />
        </div>
        {open && <SideNavbar />}
      </div>
      <div className="flex" onClick={() => setOpen(false)}>
        <div className="first">
          {blogs
            .filter((item) => item.id <= 1)
            .map((item) => (
              <div key={item.id} className="card">
                <img src={item.mainimg} alt="hi" />
                <h2>{item.title}</h2>
                <h4>{item.dis}</h4>
                <p>{item.readtime}</p>
              </div>
            ))}
        </div>

        <div>
          {blogs
            .filter((item) => item.id <= 4)
            .map((item) => (
              <div key={item.id} className="second">
                <div>
                  <div style={{ display: "flex" }} className="avatar">
                    <img src={item.avatar} alt="avatar" />
                    <p>{item.cats}</p>
                  </div>
                  <div>
                    <h3 style={{ margin: 0 }}>{item.title}</h3>
                    <p style={{ textAlign: "left" }}>{item.readtime}</p>
                  </div>
                </div>
                <div>
                  <img
                    src={item.mainimg}
                    alt="hi"
                    style={{
                      width: "120px",
                      height: "120px",
                      marginTop: 15,
                    }}
                  />
                </div>
              </div>
            ))}
        </div>

        <div className="twitter">
          <img
            src="data:image/png;base64,"
            alt="medium"
            style={{ width: "50px", height: "40px" }}
          />

          <img
            src="data:image/png;base64,"
            alt="twitter"
            style={{ width: "50px", height: "40px" }}
          />
          <p style={{ margin: "30px 0px 30px 0px" }}>
            Discover Medium writers you already <br />
            follow on twitter
          </p>
          <button className="twitbtn">
            <img
              src="data:image/png;base64,"
              alt="twitter"
              style={{ width: "30px", height: "25px", marginTop: "5px" }}
            />
            <p>Connect to twitter</p>
          </button>
          <div>
            <Link>Maybe Later</Link>
          </div>
          <div>
            <h3>Topics you follow</h3>
          </div>

          <div className={classes.root}>
            <div>
              <Button variant="contained">Javascript</Button>
              <Button variant="contained">Programming</Button>
            </div>
            <div>
              <Button variant="contained">React</Button>
              <Button variant="contained">Data Science</Button>
            </div>
          </div>
        </div>
      </div>

      <br />
      <hr />

      <div className={styles.trending}>
        <h5>
          <AiOutlineCheckCircle /> TRENDING ON MEDIUM
        </h5>
        <div>
          <TrendingStories />
        </div>
      </div>

      <Bottom />
    </div>
  );
}

export default Main;
